.transfer-card{display:flex;flex-direction:column;gap:24px;color:rgba(255,255,255,.5411764706);width:100%}.transfer-header{display:flex;justify-content:space-between;gap:50px}.transfer-header__headline{display:flex;justify-content:center;gap:20px}.transfer-header__title-icon{width:48px}.transfer-header__text{display:flex;flex-direction:column;gap:2px}.transfer-header__title{font-weight:600;font-size:20px;color:#e2e2e2}.transfer-header__descr{font-size:14px}.transfer-header__icons-card{display:flex;gap:24px}.transfer-header__icon-card{display:flex;width:48px;height:48px}.transfer-main{position:relative;display:flex;justify-content:center;align-items:center;flex-direction:column;gap:24px;padding:24px 0;background:#212121 url(http://localhost:3002/static/media/map.0782983….png) no-repeat top center}.transfer-main__img{width:160px;height:200px;position:absolute;bottom:0;right:250px}.transfer-main__img img{width:100%;height:100%}.transfer-form{display:flex;align-items:center;flex-direction:column;gap:20px;width:328px}.transfer-form input[type=number]::-webkit-outer-spin-button,.transfer-form input[type=number]::-webkit-inner-spin-button{-webkit-appearance:none;margin:0;width:0}.transfer-form__descr{width:90%}.transfer-form__prompt{color:rgba(255,255,255,0.5411764706);font-size:12px}.transfer-form__prompt-green{cursor:pointer;color:#8dc634}.transfer-form__prompt-green:hover{text-decoration:underline}.transfer-form__confirmation{background-color:#8dc641;color:#000;border-radius:8px;padding:10px 16px;font-weight:600;text-align:center;border:none;font-size:16px;transition:all .3s ease 0s;cursor:pointer;font-weight:600}.transfer-form__confirmation:hover{background-color:#699f25}